a.btn-social,
.btn-social {
  border-radius: 50%;
  color: #fff !important;
  display: inline-block;
  height: 40px;
  line-height: 40px;
  margin: 8px 2px;
  text-align: center;
  text-decoration: none;
  transition: background-color 0.3s;
  //webkit-transition: background-color .3s;
  width: 40px;
}

.btn-social .fa,
.btn-social i {
  backface-visibility: hidden;
  //moz-backface-visibility: hidden;
  //ms-transform: scale(1);
  //o-transform: scale(1);
  transform: scale(1);
  transition: all 0.25s;
  //webkit-backface-visibility: hidden;
  //webkit-transform: scale(1);
  //webkit-transition: all 0.25s;
}

.btn-social:hover,
.btn-social:focus {
  color: #fff;
  outline: none;
  text-decoration: none;
}

.btn-social:hover .fa,
.btn-social:focus .fa,
.btn-social:hover i,
.btn-social:focus i {
  //ms-transform: scale(1.3);
  //o-transform: scale(1.3);
  transform: scale(1.3);
  //  webkit-transform: scale(1.3);
}

.btn-social.btn-xs {
  font-size: 9px;
  height: 24px;
  line-height: 13px;
  margin: 6px 2px;
  width: 24px;
}

.btn-social.btn-sm {
  font-size: 13px;
  height: 36px;
  line-height: 18px;
  margin: 6px 2px;
  width: 36px;
}

.btn-social.btn-lg {
  font-size: 22px;
  height: 72px;
  line-height: 40px;
  margin: 10px 6px;
  width: 72px;
}

